@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700&family=Work+Sans:wght@400;600&display=swap);
* {
    font-family: 'Work Sans', sans-serif;

    margin: 0;
    padding: 0;

    box-sizing: border-box;
}

body {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

body::-webkit-scrollbar {
    display: none;
}

:root {
    --white: #fff;
    --black: #222;
}

.main {
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.movie__container {
    width: 100%;
    height: 100%;

    margin: 80px;

    display: grid;
    grid-template-columns: repeat(2, 550px);

    grid-column-gap: 20px;

    column-gap: 20px;
    grid-row-gap: 20px;
    row-gap: 20px;
}

.movie__grid {
    width: 100%;
    height: 100%;

    padding: 10px;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.2);

    grid-column-gap: 20px;

    column-gap: 20px;
}

.movie__title__frame {
    font-size: 14px;
}

.movie__summary {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.movie__image {
    width: 100%;
}

.movie__info__frame {
    display: grid;
    grid-template-rows: repeat(3, 1fr);
}

.movie__genres {
    list-style: none;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.movie__genres {
    margin-top: 10px;
}

@media (max-width: 1024px) {
    .movie__container {
        grid-template-columns: repeat(1, 500px);
        grid-template-rows: repeat(2, 1fr);
    }
}

@media (max-width: 768px) {
    .movie__container {
        grid-template-columns: repeat(1, 300px);
        grid-template-rows: repeat(2, 1fr);
    }

    .movie__grid {
        grid-template-columns: repeat(1, 1fr);
    }

    .movie__title__frame {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}
.loading__frame {
    width: 100%;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;
}

.loading__text {
    font-size: 30px;
    font-weight: 700;

    color: #333;
}
header {
    width: 100%;
    height: 72px;
    
    position: absolute;

    display: flex;
    justify-content: center;
    align-items: center;

    top: 0;
    left: 0;
}

.nav__width {
    width: 1120px;
    
    display: flex;
    justify-content: space-between;
    align-items: center;

    transition: width 0.5s ease-in-out;
}

.nav__width h1 {
    letter-spacing: 0.1em;
}

.nav__width h1 a {
    text-decoration: none;
    color: var(--black);
}

.nav__menu {
    list-style: none;

    display: flex;
    justify-content: center;
    align-items: center;
}

.nav__menu li {
    font-size: 20px;
    margin-left: 10px;

    color: var(--black);
    text-transform: uppercase;
}

.nav__menu li a {
    text-decoration: none;

    color: var(--black);
}

.nav__icon {
    display: none;
}

.nav__icon i {
    font-size: 20px;

    color: var(--black);
}

.submenu {
    width: 100%;
    height: 100vh;

    position: fixed;
    top: 0;
    left: 0;

    background-color: white;

    transition: left 0.5s ease-in-out;
}

.submenu.active {
    left: -100%;
}

.submenu i {
    font-size: 20px;

    position: absolute;
    top: 10px;
    right: 10px;
}

.submenu__list {
    width: 100%;
    list-style: none;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.submenu__list li {
    text-align: center;
    text-transform: uppercase;
    font-size: 20px;

    width: 100%;

    padding: 5px 0;
}

.submenu__list li:hover {
    background-color: var(--black);
    color: var(--white);
}

@media (max-width: 1024px) {
    .nav__width {
        width: 500px;
    }
}

@media (max-width: 768px) {
    .nav__width {
        width: 300px;
    }

    .nav__menu {
        display: none;
    }
    
    .nav__icon {
        display: block;
    }
}
